<template>
  <ec-drawer title="详细" :visible="visible" size="large" @close="onclose" class="ec-draw-form">
    <ec-container height="100%">
      <ec-container-header>
        <ec-panel-item>
          <ec-panel-item-left>
            <ec-header title="分配明细" icon="onetomany" color-code="008" />
          </ec-panel-item-left>
          <ec-panel-item-right>
            <div class="year">调拨年度：{{ tranYear }}年；重量单位：{{ weightUnitName }}</div>
          </ec-panel-item-right>
        </ec-panel-item>
      </ec-container-header>

      <ec-container-body>
        <ec-panel>
          <ec-panel-body>
            <ec-v-layout>
              <ec-v-item>
                <ec-table ref="table" border :data="tableData" :loading="loading" row-select>
                  <ec-table-col title="序号" type="seq" width="70" align="center" />

                  <ec-table-col
                    field="leafTypeName"
                    title="烟叶类型"
                    align="right"
                    min-width="150"
                  />
                  <ec-table-col field="leafFormName" title="烟叶形态" align="right" width="150" />
                  <ec-table-col field="packType" title="包装类型" align="left" width="150">
                    <template #default="{ row }">
                      <div v-for="packTypeObj in packTypeList" :key="packTypeObj.enumKey">
                        <span v-if="row.packType === packTypeObj.enumKey">
                          {{ packTypeObj.name }}</span
                        >
                      </div>
                    </template>
                  </ec-table-col>
                  <ec-table-col field="spec" title="规格（KG/件）" align="right" width="150" />

                  <ec-table-col field="allotNum" title="分配码包数量" align="right" width="150" />
                  <ec-table-col field="collectNum" title="领用码包数量" align="right" width="150" />
                </ec-table>
              </ec-v-item>
            </ec-v-layout>
          </ec-panel-body>
        </ec-panel>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button @click="onclose">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </ec-drawer>
</template>

<script>
export default {
  name: 'ExecutionEnterManage',
  components: {},
  data() {
    return {
      visible: false,
      queryItems: {
        leafTypeCode: '10',
        submitStateFlag: true,
        busiYear: '',
      },

      loading: false,
      leafTypeCd: '',
      tranYear: '',
      oldDataRow: [],
      orgCode: '',

      packTypeList: [],
      weightUnitName: '',
      weightUnit: '',
      tableData: {
        // 数据表格
        // totalRows: 0,
        rows: [],
      },
    };
  },

  created() {
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM'; // 获取当前登入信息
  },

  methods: {
    // 数据初始化
    // 显示推窗
    show(data) {
      this.visible = true;
      const tableData = data.tableData || [];
      this.tableData = tableData;
      this.packTypeList = data.packTypeList || [];
      const selectedRow = data.selectedRow || {};
      this.tranYear = selectedRow.tranYear;
      this.weightUnitName = data.weightUnitObj.weightUnitName;
      this.weightUnit = data.weightUnitObj.weightUnitCd;
    },

    // 合计数据的处理
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field]);
      });
      return count.toFixed(2);
    },
    // 底部合计行
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return '合计';
          }
          if (['collectNum'].includes(column.property)) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },

    // 取消
    onclose() {
      this.tableData = [];
      this.visible = false;
    },
  },
};
</script>
<style lang="less" scoped>
.year {
  font-size: 18px;
  line-height: 22px;

  color: #18191a;
  font-weight: 700;
  padding-top: 18px;
  padding-right: 50px;
  font-family: '微软雅黑 Bold', '微软雅黑', 'Microsoft YaHei', sans-serif;
}
</style>
